import { AuthService } from 'src/app/services/auth.service';
import { ToastService } from 'src/app/services/toast.service';
import { ApiCRMService } from 'src/app/services/api-crm.service';
import { CallNumber } from '@ionic-native/call-number/ngx';
import { StorageService } from 'src/app/services/storage.service';
import { HeaderTitleService } from 'src/app/services/header-title.service';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiLMSService } from 'src/app/services/api-lms.service';
import { environment } from 'src/environments/environment.prod';

@Component({
  selector: 'app-test-result',
  templateUrl: './test-result.page.html',
  styleUrls: ['./test-result.page.scss'],
})
export class TestResultPage implements OnInit {

  studentId = "";
  assignQbResultId = "";
  arrResult = {'dateAdded':null,'totalQuestion':0,'totalCorrect':0,'totalWrong':0,'unansQuestion':0};
  scholarshipStartDate = "";
  score = 0;
  maxPercent : number;  
  StudentName = "";

  isModalOpen:boolean;

  arrScholarships = [];
  errMsg="";

  arrCounsellor = [];

  errorCount = 0;
  arrUserData = [];
  constructor(
    private headerTitleService : HeaderTitleService,
    private route: ActivatedRoute,
    private apiLMSService : ApiLMSService,
    private storage : StorageService,    
    private callNumber : CallNumber,
    private apiCRMService : ApiCRMService,
    private toastr : ToastService,
    private auth : AuthService
  ) { 
    headerTitleService.title="Test Result";
  }

  ngOnInit() {
    this.studentId = this.route.snapshot.params['studentId'];
    this.assignQbResultId = this.route.snapshot.params['assignQbResultId'];
    console.log(this.studentId);
    console.log(this.assignQbResultId);
    this.getResult();
    this.getUserDataStorage();
    this.getCounsellor();
    
  }

  getResult(){
    var data = new FormData();
    data.append("xAction", "getScholarshipTestResult");    
    data.append("apiKey", "U0NJVFlSRVdBUkRa");
    data.append("assignQbResultID", this.assignQbResultId);
    data.append("studentID", this.studentId);
      this.apiLMSService.getTestResult(data).then(resp => {
        var objData = resp;
        this.arrResult = resp.data[0];
        this.maxPercent = this.arrResult["totalQuestion"] * 4;        
        console.log(this.arrResult);
        this.score = (this.arrResult["totalCorrect"] * 4) - this.arrResult["totalWrong"];
        console.log("Score :" + this.score);
        this.setCRMLeadData();
      })
  }

  async getUserDataStorage() {
    await this.storage.getObject("userdata").then((resp: any) => {
      this.arrUserData = resp;
      this.StudentName = resp["firstName"];      
    })
  }

  onClickScholarship(){
    //this.router.navigate(['/home/scholarship/scholarship-test/available-scholarships', this.studentId, this.assignQbResultId])

    var resultdate = new Date(this.arrResult["dateAdded"]).getTime();
    var timestamp = new Date().getTime();
    var diff = (timestamp - resultdate)/(24 * 60 * 60 * 1000);
    if(diff > 30){
      this.errMsg = "Sorry! The validity of the one time scholarship you won has expired. It was open for you for 30 days from the test date."
    }
    else{
      this.arrScholarships = this.apiLMSService.arrScholarships.filter(x=> this.score >= x.minMarks && this.score <= x.maxMarks );          
      if(this.arrScholarships.length ==0){
        this.errMsg = "Sorry, your score is too low. You are not eligible for any scholarship. Please talk to our counsellor for any other options";
      }
      this.isModalOpen = true;
    }
  }
  modalClose() {
    this.isModalOpen = false;        
  }

  //#region Click to Call Start
  onClickCall() {
    this.callNumber.callNumber(this.arrCounsellor["empMob1"], true)
      .then(res => console.log('Launched dialer!', res))
      .catch(err => console.log('Error launching dialer', err));
  }
  //#endregion
  
  getCounsellor() {
    this.storage.getObject("counsellorData").then((resp: any) => { this.arrCounsellor = resp })
  }

  //#region CRM Update
  // setCRMRawData(){ 
  //   this.apiCRMService.fnGetCRMRawData().then(resp=>{
  //     if(resp == true){
  //       this.apiCRMService.arrCRMRawData["IsScholarshipTest"] = true;
  //       this.apiCRMService.fnSetCRMRawData();
  //     }
  //   });
  // }
  
  //#endregion


   //#region Update CRM Data
   async setCRMLeadData() {
    if(this.errorCount > 2){
      this.toastr.toastError("Sorry, Unable to proceed your request. Kindly contact support team.");
      this.errorCount = 0;
      return false;
    }
    this.errorCount ++;
    var leadId = (this.arrUserData["leadID"] == 0) ? "" : this.arrUserData["leadID"];    
    if (leadId == "") {
      var arrPostdata = this.apiCRMService.arrCRMLeadData;
      var mobileNoCode = this.arrUserData["mobileNo"].substring(1, 3);
      var mobileNo = this.arrUserData["mobileNo"].replace('+91', '');
      arrPostdata["leadID"] = (this.arrUserData["leadID"] == 0) ? "" : this.arrUserData["leadID"];
      arrPostdata["rawLeadId"] = this.arrUserData["rawLeadID"];
      arrPostdata["productDepartmentId"] = arrRawData["productDepartmentId"];
      arrPostdata["subProductDepartmentId"] = arrRawData["subProductDepartmentId"];
      arrPostdata["fname"] = this.arrUserData["firstName"];
      arrPostdata["lname"] = this.arrUserData["lastName"];
      arrPostdata["emailId1"] = this.arrUserData["emailId"];
      arrPostdata["contactWhatsappCode"] = mobileNoCode;
      arrPostdata["contactWhatsapp"] = mobileNo;
      arrPostdata["contactOtherCode"] = mobileNoCode;
      arrPostdata["contactOther"] = mobileNo;      
      arrPostdata["leadPriorityId"] = this.apiCRMService.enumPriority.Warm;
      arrPostdata["sysLeadPriorityID"] = this.apiCRMService.enumPriority.Hot;
      arrPostdata["leadSourceId"] = environment.defaultLeadSourceId;
      arrPostdata["countryId"] = this.arrUserData["countryID"];
      arrPostdata["stateId"] = this.arrUserData["stateID"];
      arrPostdata["districtId"] = this.arrUserData["districtID"];     

      var arrRawData = Object.assign({}, this.apiCRMService.arrCRMRawData);
      arrPostdata["franchiseeId"] = arrRawData["franchiseeId"];
      arrPostdata["sysProductDepartmentId"] = arrRawData["sysProductDepartmentId"];
      arrPostdata["sysSubProductDepartmentId"] = arrRawData["sysSubProductDepartmentId"];
      arrPostdata["studentEducationId"] = arrRawData["studentEducationId"];
      arrPostdata["studentEducationYear"] = arrRawData["studentEducationYear"];
      arrPostdata["isAcademyApp"] = arrRawData["isAcademyApp"];
      arrPostdata["predictorPackage"] = arrRawData["predictorPackage"];
      arrPostdata["isScholarshipTest"] = true;
      arrPostdata["lmsCourse"] = arrRawData["LMSCourse"];

      //arrPostdata["VisitRemark"] = "This student wants to visit " + this.selectedBranchName+ " branch on " + this.branchVisitTime + " for program" + programType;

      this.apiCRMService.arrCRMLeadData = arrPostdata;
      this.apiCRMService.fnSetCRMLeadData().then((arrLeadData:any) => {
        if (arrLeadData != 0) {
          this.setCRMRawData(arrLeadData['leadId']);     
          this.updateAppRegistration(arrLeadData['leadId']);
        }
      }).catch(ex=> {        
        this.toastr.toastError("Sorry, Unable to proceed your request. Kindly contact support team.");
      });
    }
    else {
      this.apiCRMService.fnGetCRMLeadData().then(resp => {
        if (resp == true) {
          this.errorCount = 0;          
          this.apiCRMService.arrCRMLeadData["isScholarshipTest"] = true;
          if(this.apiCRMService.arrCRMLeadData.sysLeadPriorityID < 4 || this.apiCRMService.arrCRMLeadData.sysLeadPriorityID == null){
            this.apiCRMService.arrCRMLeadData["sysLeadPriorityID"] = this.apiCRMService.enumPriority.Hot;
          }          
          this.apiCRMService.fnSetCRMLeadData();
        }
        else{
          this.arrUserData["leadID"] = 0;
          this.storage.setObject("userdata", this.arrUserData);
          this.setCRMLeadData();          
          //this.isLoading = false;
        }
      }).catch(ex=> {
        this.toastr.toastWarning("CRM data not updated");
      })
    }
  }
  setCRMRawData(leadId) {
    this.apiCRMService.fnGetCRMRawData().then(resp => {
      if (resp == true) {        
        this.apiCRMService.arrCRMRawData["isScholarshipTest"] = true;
        this.apiCRMService.arrCRMRawData["leadId"] = leadId;        
        this.apiCRMService.fnSetCRMRawData();
      }
    });
  }  
  //#endregion

   //#region  update App Registration
   updateAppRegistration(leadId) {
    this.storage.getObject("userdata").then(respUser => {
      respUser["leadID"] = leadId;
      this.storage.setObject("userdata", respUser);
    })    
    this.auth.getAppRegistration().then(resp => {
      var regData = resp;
      regData["leadID"] = leadId;
      this.auth.putAppRegistration(JSON.stringify(regData)).then(resp => this.apiCRMService.updateSysLeadPriority(leadId));
    })
  }
  //#endregion



}
